<template>

  <div style="display: flex; justify-content: center;">
    <div id="watchRequest">
      <div>
        <label>express: </label>
        <input type="text" v-model="express">
      </div>

      <div>
        <label>expand: </label>
        <input type="text" v-model="expand">
      </div>
    </div>
  </div>

  <button type="primary" @click="sendObject">getInstance</button>

  <div>
    <h3 class="preserve-whitespace">{{ObjectResponse}}</h3>
  </div>

</template>

<script>
// grpc_web

import {
  ObjectServiceClient
} from '@/assets/proto/ArthasService_grpc_web_pb';

import { ObjectRequest } from '@/assets/proto/ArthasService_grpc_web_pb';

export default {
  name: 'HelloWorld',
  data(){
    return{
      client : null,
      express: "instances[0]",
      expand: 4,
      ObjectResponse: "www"
    }
  },

  created() {
    this.client = new ObjectServiceClient('http://localhost:8567');
    this.metadata = {"Content-Type": "application/grpc-web-text"};
  },

  methods:{

    sendObject(){
      var objectRequest = new ObjectRequest();
      objectRequest.setExpress(this.express).setExpand(this.expand);
      this.client.getInstance(objectRequest,{},(error, response) => {
            if (!error) {
              console.log(response.getValue());
              this.ObjectResponse = response.getValue()
            }else {
              console.log(error);
            }
      })
    }

  }
}

</script>



