  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}

 #watchRequest {
   display: flex;
   flex-direction: column;
   align-items: flex-start; /*  */
   justify-content: center
 }
label {
  margin-right: 10px; /*  */
  align-items: flex-start; /*  */
}

table {
  border-collapse: collapse;
  width: 100%;
}

th, td {
  border: 1px solid #ccc;
  padding: 8px;
  text-align: left;
}

th {
  background-color: #f2f2f2;
}
.preserve-whitespace {
  white-space: pre-wrap; /*  */
  text-align: left;
}
</style>
